<template>
	<div :class="containerClass" @click="onWrapperClick">
        <div v-if="isAuthenticated"> 
        <AppTopBar @menu-toggle="onMenuToggle" />
        <div class="layout-sidebar" @click="onSidebarClick">
            <AppMenu :model="menu" @menuitem-click="onMenuItemClick" />
        </div>

        <div class="layout-main-container">
            <div class="layout-main">
                <router-view />
            </div>
            <AppFooter />
        </div>

		<AppConfig :layoutMode="layoutMode" :layoutColorMode="layoutColorMode" @layout-change="onLayoutChange" @layout-color-change="onLayoutColorChange" />
        <transition name="layout-mask">
            <div class="layout-mask p-component-overlay" v-if="mobileMenuActive"></div>
        </transition>
        </div>
        <div v-else>
            <div class="layout-main-container">
            <div class="layout-main">
                <div class="grid">
                    <div class="col-12 md:col-6">
                        <div class="card p-fluid">
                            <h5>Inicio de session</h5>
                            <div class="field grid">
                                <label for="name3" class="col-12 mb-2 md:col-2 md:mb-0">Usuario</label>
                                <div class="col-12 md:col-10">
                                    <InputText id="name3" type="text" v-model="user.name"/>
                                </div>
                            </div>
                            <div class="field grid">
                                <label for="email3" class="col-12 mb-2 md:col-2 md:mb-0">Contrase√±a</label>
                                <div class="col-12 md:col-10">
                                    <InputText id="email3" type="password" v-model="user.pass"/>
                                </div>
                            </div>
                            <div class="field grid">                               
                                <div class="col-12 md:col-12">
                                    <Button label="Ingresar" @click="login(user.name,user.pass)"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

               
        </div>
	</div>
</template>

<script>
import AppTopBar from './AppTopbar.vue';
import AppMenu from './AppMenu.vue';
import AppConfig from './AppConfig.vue';
import AppFooter from './AppFooter.vue';
import LoginService from './service/LoginService';

export default {
    data() {
        return {
            isAuthenticated :true,
            user:{},
            layoutMode: 'static',
            layoutColorMode: 'light',
            staticMenuInactive: false,
            overlayMenuActive: false,
            mobileMenuActive: false,
            menu : [
                {
                    label: 'Inicio',
                    items: [{
                        label: 'Informe', icon: 'pi pi-fw pi-home', to: '/'
                    }]
                },
				{
					label: 'Servicios', icon: 'pi pi-fw pi-sitemap',
					items: [
                        {label: 'Mensajes', icon: 'pi pi-fw pi-id-card', to: '/message'},
                        {label: 'Ventas', icon: 'pi pi-fw pi-id-card', to: '/ventas'},
                        {label: 'Notificaciones', icon: 'pi pi-fw pi-id-card', to: '/push'},
						
					]
				},
            ]
        }
    },
    loginServices : null,
    created() {
		this.loginServices = new LoginService();
	},
	mounted() {
		
	},
    watch: {
        $route() {
            this.menuActive = false;
            this.$toast.removeAllGroups();
        }
    },
    methods: {
        onWrapperClick() {
            if (!this.menuClick) {
                this.overlayMenuActive = false;
                this.mobileMenuActive = false;
            }

            this.menuClick = false;
        },
        onMenuToggle() {
            this.menuClick = true;

            if (this.isDesktop()) {
                if (this.layoutMode === 'overlay') {
					if(this.mobileMenuActive === true) {
						this.overlayMenuActive = true;
					}

                    this.overlayMenuActive = !this.overlayMenuActive;
					this.mobileMenuActive = false;
                }
                else if (this.layoutMode === 'static') {
                    this.staticMenuInactive = !this.staticMenuInactive;
                }
            }
            else {
                this.mobileMenuActive = !this.mobileMenuActive;
            }

            event.preventDefault();
        },
        onSidebarClick() {
            this.menuClick = true;
        },
        onMenuItemClick(event) {
            if (event.item && !event.item.items) {
                this.overlayMenuActive = false;
                this.mobileMenuActive = false;
            }
        },
		onLayoutChange(layoutMode) {
			this.layoutMode = layoutMode;
		},
		onLayoutColorChange(layoutColorMode) {
			this.layoutColorMode = layoutColorMode;
		},
        addClass(element, className) {
            if (element.classList)
                element.classList.add(className);
            else
                element.className += ' ' + className;
        },
        removeClass(element, className) {
            if (element.classList)
                element.classList.remove(className);
            else
                element.className = element.className.replace(new RegExp('(^|\\b)' + className.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
        },
        isDesktop() {
            return window.innerWidth >= 992;
        },
        isSidebarVisible() {
            if (this.isDesktop()) {
                if (this.layoutMode === 'static')
                    return !this.staticMenuInactive;
                else if (this.layoutMode === 'overlay')
                    return this.overlayMenuActive;
            }

            return true;
        },
        login(){           
           this.loginServices.getAuthLogin(this.user.name,this.user.pass).then(res=>{
                console.log(res.code);
                this.loginServices.getAuthToken(res.code,this.user.name).then(res=>{
                    localStorage.token = res.accessToken;
                    this.isAuthenticated = true;
                }).catch(error => console.error(error));
           });                   
            
        }
    },
    computed: {
        containerClass() {
            return ['layout-wrapper', {
                'layout-overlay': this.layoutMode === 'overlay',
                'layout-static': this.layoutMode === 'static',
                'layout-static-sidebar-inactive': this.staticMenuInactive && this.layoutMode === 'static',
                'layout-overlay-sidebar-active': this.overlayMenuActive && this.layoutMode === 'overlay',
                'layout-mobile-sidebar-active': this.mobileMenuActive,
				'p-input-filled': this.$primevue.config.inputStyle === 'filled',
				'p-ripple-disabled': this.$primevue.config.ripple === false,
                'layout-theme-light': this.$appState.theme.startsWith('saga')
            }];
        },
        logo() {
            return (this.layoutColorMode === 'dark') ? "images/logo-white.svg" : "images/logo.svg";
        }
    },
    beforeUpdate() {
        if (this.mobileMenuActive)
            this.addClass(document.body, 'body-overflow-hidden');
        else
            this.removeClass(document.body, 'body-overflow-hidden');
    },
    components: {
        'AppTopBar': AppTopBar,
        'AppMenu': AppMenu,
        'AppConfig': AppConfig,
        'AppFooter': AppFooter,
    }
}
</script>

<style lang="scss">
@import './App.scss';
</style>
