import { createRouter, createWebHashHistory } from 'vue-router';
import Dashboard from './components/Dashboard.vue';

const routes = [
    {
        path: '/',
        name: 'dashboard',
        component: Dashboard,
    }, 
    {
        path: '/push',
        name: 'push',
        component: () => import('./pages/CrudNotificaciones.vue'),
    },
    {
        path: '/ventas',
        name: 'ventas',
        component: () => import('./pages/CrudVentas.vue'),
    },
    {
        path: '/message',
        name: 'message',
        component: () => import('./pages/CrudMessage.vue'),
    },   
   
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
});

export default router;
